const INCREMENT = 'INCREMENT'
export default {
  [INCREMENT](state, res) {
    // console.log('进入mutation');
    state.getMineBaseMsg = { ...state.getMineBaseMsg, msg: res}
    // res.data.msg
    // console.log('进入mutations修改state成功');
    // console.log(state.getMineBaseMsg.msg.category)
  }
}



