import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './action'
import api from './api'

Vue.use(Vuex)

const state = {
  count: 0,
  getMineBaseMsg: {
    errno: 1,
    msg: {}
  }
}

const getters = {
  getMineBaseMsg: state => state.getMineBaseMsg
}

export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters
});



