import api from './api'

export default {
  getMineBaseApi ({commit}){
    console.log(commit)
    // console.log("进入action");
    api.mineBaseMsgApi()
      .then(res => {
        // console.log('action中调用封装后的axios成功')
        commit('INCREMENT', res)
      })
  }
}





