<template>
  <div>
    <div class="index-top">
      <div class="index-top-logo"></div>
      <div class="index-top-search">
        <i class="iconfont icon-search"></i>
        <p>查找宝贝</p>
      </div>
    </div>
    <div class="index-classify">
      <div class="index-classify-nei">
        <ul>
          <li v-for="item in category">
            <router-link :to="item.ld">{{ item.name }}</router-link>
          </li>
        </ul>
      </div>
    </div>
    <div class="index-content">
      <carousel v-bind:slides="slides"></carousel>
      <miscellaneous></miscellaneous>
      <goods-wall></goods-wall>
    </div>
  </div>
</template>

<script>
  // 红点着重
    import { mapGetters } from 'vuex'
    import carousel from '../components/Carousel'
    import miscellaneous from '../components/Miscellaneous'
    import goodsWall from '../components/GoodsWall'

    export default {
        name: "index",
        components: {
          carousel,
          miscellaneous,
          goodsWall
        },
        data: function (){
          return {
            state: this.$store.state,
          }
        },
        created: function (){
          this.$store.dispatch('getMineBaseApi');
        },
        methods: {
          // submitForm
        },
        computed: {
          ...mapGetters([
            'getMineBaseMsg'
          ]),
          category: function(){
            console.log(this.state)
            return this.state.getMineBaseMsg.msg.category
          },
          slides: function () {
            return this.state.getMineBaseMsg.msg.carousel
          }
        }
    }
</script>

<style scoped>
  .index-top{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 0.8rem;
    background: #ff464e;
    z-index: 100;
  }
  .index-top-logo{
    width: 40%;
    height: 100%;
    background: url("../assets/logo.png") no-repeat;
    background-size: 100% 100%;
    float: left;
  }
  .index-top-search{
    width: 55%;
    height: 0.6rem;
    margin-top: .1rem;
    margin-left: .2rem;
    color: #fff;
    background: #fd7f7f;
    float: left;
  }
  .index-top-search i{
    margin-top: .15rem;
    margin-left: .1rem;
    font-size: .32rem;
    display: block;
    float: left;
  }
  .index-top-search p{
    margin-top: .16rem;
    margin-left: .5rem;
    font-size: .28rem;
  }

  .index-classify{
    overflow: hidden;
    position: fixed;
    top: .8rem;
    width: 100%;
    height: .6rem;
    background: #fff;
    z-index: 99;
  }
  .index-classify-nei{
    overflow-y: hidden;
    overflow-x: scroll;
    width: 100%;
    height: .9rem;
  }
  .index-classify-nei ul{
    padding-top: .16rem;
    overflow: hidden;
    width: 20rem;
  }
  .index-classify-nei ul li{
    padding: 0 .25rem;
    color: #000;
    font-size: .28rem;
    float: left;
    display: block;
  }
  .index-classify-nei ul li a{

  }

  .index-content{
    overflow: hidden;
    margin-top: 1.4rem;
    margin-bottom: 1.05rem;
  }
</style>



