import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/'

import axios from 'axios'
Vue.prototype.$http = axios
Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})





